var  channels = new Map()
//var  subregions = new Map()

var root = { code: '0', acronyme:'', name: 'root', children:[]}

channels.set( 0 , root)
dac_channels.forEach( (line) =>{
  line.children = []
  var parentCode
  var parent
    parentCode = parseInt(line.codeParent)
    if (channels.has(parentCode)){
       channels.set(parseInt(line.code), line)
       parent = channels.get(parentCode)
       parent.children.push(line)

    }
} )


var fn = function(node){
  console.log( ` code ${node.code}  val ${node.name} parent ${node.codeParent} /n` )
  if (node.children){
   node.children.forEach( ch => fn(ch))
  }
}

