var  sectors = new Map()

var root = { code: '0', name: 'root', children:[]}

sectors.set( 0 , root)

dac_sectors.forEach( (line) =>{
  line.children = []
  var parentCode
  var parent
    parentCode = parseInt(line.parent)
    if (sectors.has(parentCode)){
       sectors.set(parseInt(line.code), line)
       parent = sectors.get(parentCode)
       parent.children.push(line)
    }
} )


var fn = function(node){
  console.log( ` code ${node.code}  val ${node.name} parent ${node.parent}` )
  if (node.children){
   node.children.forEach( ch => fn(ch))
  }
}

//fn(root)

export default root



